.Welcome {
    margin: 85px auto 0 auto;
    width: 35%;
    color: white;
    background-color: #404040;
    text-align: center;
    border-radius: 20px;
    padding: 10px;
}

.Personal_Description {
    font-size: 20px;
    padding: 10px;
    padding-top: 15px;
}

.Pictures > img {
    max-width: 200px;
    max-height: 200px;
    padding: 15px;
}

.Pictures {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 10px;
}
